  --ingredients-bcgrnd: #9cbe8f;
  --background-img: #d9d9d9;
  --txt-color-blck: #131313;
  --background-gre: #84a178;
  --product-color-gre: #aec6a5;
  --copyright-color: #7a7a7a;
}

body {
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, ol, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, a {
  cursor: pointer;
}

input, textarea {
  box-sizing: border-box;
  outline: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 320px;
    padding-left: 22px;
    padding-right: 22px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: .2;
  visibility: hidden;
  pointer-events: none;
  transform: translate(0%, 100%);
}

.button {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: 12px 34px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.29;
  display: flex;
}

.button:disabled {
  cursor: not-allowed;
}

.first-type__button {
  height: 42px;
  border: 2px solid var(--button-color-yel);
  background: none;
  border-radius: 10px;
}

.first-type__button:hover {
  border: 2px solid var(--button-color-green);
}

.first-type__button:focus {
  color: var(--txt-color-btnwhi);
  border: 2px solid var(--button-color-green);
  outline: none;
}

.first-type__button:disabled {
  border: 2px solid var(--button-color-gre);
  color: var(--txt-color-blck);
}

.second-type__button {
  height: 42px;
  background: var(--button-color-yel);
  border: none;
  border-radius: 10px;
}

.second-type__button:hover {
  background: var(--button-color-green);
}

.second-type__button:focus {
  background: var(--button-color-green);
  color: var(--txt-color-btnwhi);
  outline: var(--button-color-green);
}

.second-type__button:disabled {
  background: var(--button-color-gre);
  color: var(--txt-color-blck);
}

.third-type__button {
  height: 40px;
  color: var(--button-color-yel);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  text-decoration: underline;
}

.third-type__button:hover {
  color: var(--button-color-green);
}

.third-type__button:focus {
  color: var(--txt-color-btnwhi);
}

.third-type__button:disabled {
  color: var(--button-color-gre);
}

.fourth-type__button {
  height: 42px;
  border: 2px solid var(--button-color-yel);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
}

.fourth-type__button:hover {
  border: 2px solid var(--button-color-green);
}

.fourth-type__button:focus {
  color: var(--txt-color-btnwhi);
  border: 2px solid var(--button-color-green);
  outline: none;
}

.fourth-type__button:disabled {
  border: 2px solid var(--button-color-gre);
  color: var(--txt-color-blck);
}

.button__icon {
  width: 12px;
  height: 12px;
  stroke-width: 0;
  stroke: currentColor;
  fill: rgba(0, 0, 0, 0);
  margin-left: 12px;
  display: inline-block;
}

@media screen and (max-width: 1199px) {
  .first-type__button {
    height: 24px;
    border-radius: 8px;
    padding: 8px 12px;
    font-size: 8px;
    line-height: 1.25;
  }

  .third-type__button {
    height: 24px;
    padding: 6px 10px;
    font-size: 8px;
    line-height: 1.25;
  }
}

