.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .2);
    transition: opacity (250ms cubic-bezier(0.4, 0, 0.2, 1)), visibility (250ms cubic-bezier(0.4, 0, 0.2, 1)), transform (250ms cubic-bezier(0.4, 0, 0.2, 1));
}

.backdrop.is-hidden {
    opacity: 0.2;
    visibility: hidden;
    transform: translate(0%, 100%);
    pointer-events: none;
}